.burger {
  position: relative;
  height: 44px;
  width: 50px;
  padding: 2px 0px 2px 6px;
  display: none; }
  .burger_button {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .burger_line {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .burger_line::after {
    content: '';
    top: 12px; }
  .burger_line::before {
    content: '';
    top: -12px; }
  .burger_line, .burger_line::before, .burger_line::after {
    width: 100%;
    height: 5px;
    background: white;
    position: absolute; }
  .burger_button--active .burger_line {
    background-color: transparent; }
  .burger_button--active .burger_line::before {
    transform: rotate(45deg);
    top: 0; }
  .burger_button--active .burger_line::after {
    transform: rotate(-45deg);
    top: 0; }

