.slick-prev {
  left: -110px; }
  @media screen and (max-width: 1350px) {
    .slick-prev {
      left: -70px; } }
  @media screen and (max-width: 1140px) {
    .slick-prev {
      left: calc(50% - 70px); } }

.slick-prev:before {
  transform: scale(-1, 1); }

.slick-next {
  right: -110px; }
  @media screen and (max-width: 1350px) {
    .slick-next {
      right: -70px; } }
  @media screen and (max-width: 1140px) {
    .slick-next {
      right: calc(50% - 70px); } }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px; }

.slick-dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center; }

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer; }

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent; }

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none; }

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1; }

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
